import {DateTime} from 'luxon'

export default {
    data() {
        return {
            /* convert ISO date to timestamp valid, this was created because database didnt accept ISO format and the vue plugin for timestamps uses iso*/
            formatDateFromIsoToSend(date) {
                return DateTime.fromISO(date).toFormat('yyyy-MM-dd HH:MM:ss')
            },
            convertFromTimeStampsToIso(date) {

            },
            fromIso(date) {
                return this.formatDateFromIsoToSend(date);
            },
            fromTs(date) {
                return DateTime.fromISO(date.replace(" ", 'T'));
            },
            /* converte o timestamp para data pt br */
            tsToPtBrString(date, time = false) {
                let times = date.split(' ')[1];
                let dates = date.split(' ')[0];
                return time ? dates.split('-')[2] + "/" + dates.split('-')[1] + "/" + dates.split('-')[0] + " às " + times : dates.split('-')[2] + "/" + dates.split('-')[1] + "/" + dates.split('-')[0];
            }
        }
    },
    methods: this

    ,
    filters: this

}