export default{
    methods:{
        getPrograms(){
            return this.$store.dispatch('getPrograms');
        },
        companyPrograms(id){
            return this.$store.dispatch('companyPrograms',id);
        },
        getProgram(params){
            return this.$store.dispatch('getProgram',params);
        },
        getActivePrograms(){
            return this.$store.dispatch('getActivePrograms');
        }
    },
    computed:{
        programs(){
            return this.$store.state.program.list
        },
        program(){
            return this.$store.state.program.current
        },
    }
}