<template>
    <div id="company-show" class="bg-white" v-if="company.id == $route.params.id">
        <div>
            <div style="background-image: url('./img/property1.jpg')" class="container-fluid cover-container">
                <div class="cover">
                    <div class="container">
                        <div class="row">
                            <div class="col-md-8">
                                <div class="container-info">
                                    <p>
                                        <i class="dashicons dashicons-location"></i> {{company.address}},
                                        {{company.address_number}},{{company.address_complement}}</p>
                                    <p><a :href="company.site_url" target="_blank">
                                        <i class="dashicons dashicons-admin-site"></i> {{company.site_url}}</a></p>
                                    <p v-text="company.description"></p>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="container-logo text-center">
                                    <img :src="company.photo_url" alt="" class="img-fluid rounded-circle">
                                    <h1 v-text="company.display_name" class="text-white h5 text-shadow-1"></h1>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="container">
                <div class="row">
                    <div class="col-md-8 content-main padded-v">

                        <div class="row">
                            <div class="col-md-12">
                                <div class="badge-container">
                            <span class="badge  bg-grey badge-margin text-white"
                                  v-for="market in ['sertor elétrico','viagens','comércio','alimentação','educação','saúde']"
                                  v-text="market">
                            </span>
                                </div>
                                <h1> {{company.display_name}} </h1>
                                <p> {{company.description}} </p>
                                <div v-html="company.about"></div>
                            </div>
                        </div>

                        <news-component v-if="$store.state.news"></news-component>
                        <program-component v-if="company.profile == 1"></program-component>
                    </div>
                    <div class="col-md-4 content-side">

                    </div>
                </div>
            </div>

        </div>
    </div>

</template>
<script>
    import companyMixins from '../../../mixins/companies';
    import newsMixins from '../../../mixins/news';
    import programMixins from '../../../mixins/programs';
    import newsComponent from './components/News'
    import programComponent from './components/Program'

    export default {
        mixins: [newsMixins, companyMixins, programMixins],
        components: {newsComponent, programComponent},
        created() {
            this.preload(true);
            this.$store.dispatch('getCompany', this.$route.params.id).then(() => {
                this.companyNews(this.$route.params.id);
                this.companyPrograms(this.$route.params.id);
            }).finally(() => this.preload(false));
        }
    }
</script>
<style lang="scss">
    #company-show {
        .badge-container {
            margin-bottom: 10px;
            margin-top: 10px;
        }
        .cover-container {
            background-size: cover;
            background-position: center center;
        }

        .cover {
            $cover_height: 12em;
            min-height: $cover_height;
            width: 100%;
            /*background-color: rgba(0, 0, 0, 0.6);*/
            .container-info, .container-logo {
                /*width: 50%;*/
                margin-top: 5em;
                margin-bottom: 5em;
                padding: 2em;
                color: white;

            }
            .container-logo {

                img {
                    width: 30%;
                    margin-bottom: 20px;
                }
            }
            .container-info {
                background-color: rgba(0, 0, 0, 0.6);
                p {
                    a {
                        text-decoration: none;
                        color: white;
                        &:hover {
                            text-decoration: none;
                        }
                    }
                    i {
                        margin-bottom: 3px;
                    }
                }

            }
        }
        .property-price {
            background-color: rgba(0, 0, 0, 0);
        }
        .container-logo {

        }
    }

    .content-box {
        padding: 0;
    }

</style>
