<template>
    <div class="row">
        <div class="col-md-12">
            <h1>Programas</h1>
        </div>
        <div class="col-md-4 col-sm-6 padded-v" v-for="program in programs">
            <card v-if="program.company" :image="program.cover" :header-text="program.company.data.name"
                  :small-image="program.company.photo_url">
                <div class="badge badge-primary m-r" v-for="market in program.markets" v-text="market.name"></div>
                <div class="label text-left">
                    <small>Publicado em : {{
                        fromTs(program.created_at.date).toFormat(constants.dates.BASIC_WITH_HOUR).replace(" ",' às ') }}
                    </small>
                </div>
                <h2 class="h4">
                    {{ program.title }}
                </h2>
                <p>
                    {{program.description}}
                </p>
                <div class="text-center padded-v">
                    <router-link class="btn btn-primary btn-lg text-upper"
                                 :to="{name:'program.show',params:{id:program.id}}">
                        <span>Ver</span>
                    </router-link>
                </div>
            </card>

        </div>
    </div>
</template>
<script>

    import Card from '../../../../components/CustomCard'
    import programsMixis from '../../../../mixins/programs'
    import dateMixins from '../../../../mixins/dates';

    export default {
        mixins: [programsMixis, dateMixins],
        components: {
            Card,
        },
    }
</script>
